// 封装delete请求
const httpDelete =  function(url, msgManager = {}, status,config) {
    status = status ? status : 200;
    status = Array.isArray(status) ? status : [status];
    let showSuccess = true;
    let showError = true;
    if (msgManager.showOuter === 'success') {
        showError = false;
    } else if (msgManager.showOuter === 'error') {
        showSuccess = false;
    } else {
        if (msgManager.showOuter === false) {
            showSuccess = false;
            showError = false;
        }
    }
    return new Promise((resolve, reject) => {
        axios.delete(url, config)
        // 接收到了正确的response
        .then(axiosRes => {
            if (status.includes(axiosRes.data.meta.status)) {
                showSuccess && ElMessage.success({message: axiosRes.data.meta.msg, duration: 1200});
                msgManager.success && msgManager.success.forEach(element => {
                    ElMessage.success({message: element, duration: 1200 });
                });
            } else {
                showError && ElMessage.error({message: axiosRes.data.meta.msg, duration: 1200});
                msgManager.error && msgManager.error.forEach(element => {
                    ElMessage.error({message: element, duration: 1200 });
                });
            }
            resolve(axiosRes.data);
        // 未接收到response 或 接收到了错误的response
        }).catch(axiosErr => {
            ElMessage.error({message: axiosErr.message, duration: 1200 });
            msgManager.error && msgManager.error.forEach(element => {
                ElMessage.error({message: element, duration: 1200 });
            });
            // reject(axiosErr.message);
        })
    });
}

export default {
    httpGet, httpPut, httpPost, httpDelete
}