import { createRouter, createWebHistory } from 'vue-router'
// import LoginView from '../views/LoginView.vue'
// import HomeView from '../views/HomeView.vue'
// import WelcomeView from '../views/WelcomeView.vue'
const LoginView = () => import(/* webpackChunkName: "login_home_welcome" */ '../views/LoginView.vue')
const HomeView = () => import(/* webpackChunkName: "login_home_welcome" */ '../views/HomeView.vue')
const WelcomeView = () => import(/* webpackChunkName: "login_home_welcome" */ '../views/WelcomeView.vue')

// import UsersView from '../views/user/UsersView.vue'
// import RightsView from '../views/power/RightsView.vue'
// import RolesView from '../views/power/RolesView.vue'
const UsersView = () => import(/* webpackChunkName: "users_rights_roles" */ '../views/user/UsersView.vue')
const RightsView = () => import(/* webpackChunkName: "users_rights_roles" */ '../views/power/RightsView.vue')
const RolesView = () => import(/* webpackChunkName: "users_rights_roles" */ '../views/power/RolesView.vue')

// import CateView from '../views/goods/CateView.vue'
// import ParamsView from '../views/goods/ParamsView.vue'
const CateView = () => import(/* webpackChunkName: "cate_params" */ '../views/goods/CateView.vue')
const ParamsView = () => import(/* webpackChunkName: "cate_params" */ '../views/goods/ParamsView.vue')

// import ListView from '../views/goods/ListView.vue'
// import AddGoodView from '../views/goods/AddGoodView.vue'
// import EditGoodView from '../views/goods/EditGoodView.vue'
const ListView = () => import(/* webpackChunkName: "list_add_edit" */ '../views/goods/ListView.vue')
const AddGoodView = () => import(/* webpackChunkName: "list_add_edit" */ '../views/goods/AddGoodView.vue')
const EditGoodView = () => import(/* webpackChunkName: "list_add_edit" */ '../views/goods/EditGoodView.vue')

// import OrderView from '../views/order/OrderView.vue'
// import ReportView from '../views/report/ReportView.vue'
const OrderView = () => import(/* webpackChunkName: "order_report" */ '../views/order/OrderView.vue')
const ReportView = () => import(/* webpackChunkName: "order_report" */ '../views/report/ReportView.vue')

const routes = [
  { path: '/', redirect: '/login' },
  { path: '/login', component: LoginView },
  { 
    path: '/home', 
    component: HomeView,
    redirect: '/home/welcome',
    children: [
      { path: 'welcome', component: WelcomeView },
      { path: 'users', component: UsersView },
      { path: 'rights', component: RightsView },
      { path: 'roles', component: RolesView },
      { path: 'categories', component: CateView },
      { path: 'params', component: ParamsView },
      { path: 'goods', component: ListView },
      { path: 'goodsAdd', component: AddGoodView },
      { path: 'goodsEdit/:id', component: EditGoodView },
      { path: 'orders', component: OrderView },
      { path: 'reports', component: ReportView },
    ]
  },
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  // }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

// 挂载导航守卫
router.beforeEach(function (to, from, next) {
  // to 将要跳转的目标路径对象
  // from 跳转的源头路径对象
  // next 跳转函数；next() 放行；next('/login') 强制跳转到login页面
  // 如果直接访问登录页面，放行
  if (to.path === '/login') return next();
  // 获取token
  const token = window.sessionStorage.getItem("token");
  // 如果没有有效的token，强制跳转到login页面
  if (!token) return next('/login');
  // 如果有有效的token，放行
  next();
});

export default router
