import axios from 'axios'
import router from '../router/index.js'
import { ElMessage } from 'element-plus'
// 导入 NProgress 包对应的JS 和 CSS
import NProgress from 'nprogress'
// import 'nprogress/nprogress.css'

// 设置请求的baseURL
axios.defaults.baseURL = "http://127.0.0.1:8888/api/private/v1/"
// 设置请求超时 10s
axios.defaults.timeout = 10000;
// 设置post请求的请求头的Content-Type：application/x-www-form-urlencoded;charset=UTF-8
axios.defaults.headers.post["Content-Type"] = "application/x-www-form-urlencoded;charset=UTF-8";

// 设置请求拦截器
axios.interceptors.request.use(config => {
    // 在 request 拦截器 中展示页面加载的进度条
    NProgress.start();
    // 1. 在请求的头部行中加入token验证的Authorization字段
    config.headers["Authorization"] = window.sessionStorage.getItem("token");
    return config;
});

// 设置响应求拦截器
axios.interceptors.response.use(response => {
    // 在 response 拦截器 中关闭页面加载的进度条
    NProgress.done();
    // axiosResponse的状态码在 2xx 范围内都会触发该函数。
    let status = response.data.meta.status;
    if ([200, 201, 204].includes(status)) return response;

    if (status === 400) {
        // 如果是token失效，跳转到登陆页面
        if ("无效token" === response.data.meta.msg) {
            router.replace('/login');
            return ElMessage.error({message: '登录失效，请重新登录！', duration: 1200});
        }
    }
    return Promise.resolve(response);
}, error => {
    // axiosResponse的状态码在 2xx 以外都会触发该函数。
    return Promise.reject(error);
});

export default axios;